___CSS_LOADER_EXPORT___.push([module.id, `* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

header {
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    border-bottom: 2px solid #f1f1f1;
    padding: 10px 20px;
    background-color: #fff;
}

nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

#logo > img {
    height: 80px;
    width: 80px;
    margin: 0;
    margin-right: auto;
    border: solid 1px #032b55;
    border-radius: 50%;
    cursor: pointer;
}

nav button { 
    margin: 0 10px;
    padding: 10px 20px;
    font-size: 16px; 
    cursor: pointer; 
    background-color: #032b55; 
    color: white; 
    border: none; 
    border-radius: 10px; 
    transition: background-color 0.3s, transform 0.3s;
}

nav button:hover { 
    background-color: #034082; 
    transform: scale(1.03);
}
#content { 
    text-align: center; 
    font-family: Arial, sans-serif; 
    padding: 20px; 
    color: #032b55;
} 

img { 
    border-radius: 10px;
    width: 450px; 
    height: auto; 
    margin: 20px 0; 
}

.menu ul li { 
    font-size: 18px; 
    margin: 5px 0; 
    list-style: none;
    font-style: italic;
    font-weight: bold;
    transition: color 0.4s, font-size 0.2s;
    height: 26px;
}
.menu ul li:hover { 
    font-size: 19px; 
    color: #034082;  
    cursor: pointer;
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;IACI,SAAS;IACT,UAAU;IACV,sBAAsB;AAC1B;;AAEA;IACI,wCAAwC;IACxC,gCAAgC;IAChC,kBAAkB;IAClB,sBAAsB;AAC1B;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,8BAA8B;AAClC;;AAEA;IACI,YAAY;IACZ,WAAW;IACX,SAAS;IACT,kBAAkB;IAClB,yBAAyB;IACzB,kBAAkB;IAClB,eAAe;AACnB;;AAEA;IACI,cAAc;IACd,kBAAkB;IAClB,eAAe;IACf,eAAe;IACf,yBAAyB;IACzB,YAAY;IACZ,YAAY;IACZ,mBAAmB;IACnB,iDAAiD;AACrD;;AAEA;IACI,yBAAyB;IACzB,sBAAsB;AAC1B;AACA;IACI,kBAAkB;IAClB,8BAA8B;IAC9B,aAAa;IACb,cAAc;AAClB;;AAEA;IACI,mBAAmB;IACnB,YAAY;IACZ,YAAY;IACZ,cAAc;AAClB;;AAEA;IACI,eAAe;IACf,aAAa;IACb,gBAAgB;IAChB,kBAAkB;IAClB,iBAAiB;IACjB,sCAAsC;IACtC,YAAY;AAChB;AACA;IACI,eAAe;IACf,cAAc;IACd,eAAe;AACnB","sourcesContent":["* {\n    margin: 0;\n    padding: 0;\n    box-sizing: border-box;\n}\n\nheader {\n    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);\n    border-bottom: 2px solid #f1f1f1;\n    padding: 10px 20px;\n    background-color: #fff;\n}\n\nnav {\n    display: flex;\n    align-items: center;\n    justify-content: space-between;\n}\n\n#logo > img {\n    height: 80px;\n    width: 80px;\n    margin: 0;\n    margin-right: auto;\n    border: solid 1px #032b55;\n    border-radius: 50%;\n    cursor: pointer;\n}\n\nnav button { \n    margin: 0 10px;\n    padding: 10px 20px;\n    font-size: 16px; \n    cursor: pointer; \n    background-color: #032b55; \n    color: white; \n    border: none; \n    border-radius: 10px; \n    transition: background-color 0.3s, transform 0.3s;\n}\n\nnav button:hover { \n    background-color: #034082; \n    transform: scale(1.03);\n}\n#content { \n    text-align: center; \n    font-family: Arial, sans-serif; \n    padding: 20px; \n    color: #032b55;\n} \n\nimg { \n    border-radius: 10px;\n    width: 450px; \n    height: auto; \n    margin: 20px 0; \n}\n\n.menu ul li { \n    font-size: 18px; \n    margin: 5px 0; \n    list-style: none;\n    font-style: italic;\n    font-weight: bold;\n    transition: color 0.4s, font-size 0.2s;\n